  checkDataRoute(value: string): any {
    if (value === 'new') {
      return 1
    }
    if (value === 'cancel') {
      return 2
    }
    if (value === 'renew') {
      return 3
    }
    if (value === 'lapse') {
      return 4
    }
    if (value === 'contract') {
      return ''
    }
    if (value === 'pending') {
      return 3
    }
    if (value === 'refuse') {
      return 4
    }
  }

  handleFilter(value: Record<string, any>): void {
    this.showMenu = false
    let check = this.$route.name
    if (check) {
      this.query = {
        ...this.query,
        ...value,
        contractStatus: this.checkDataRoute(check),
        searchType: 'GROUP_CONTRACT'
      }
      this.query.page = 1
      this.api
        .getData(this.query)
        .then((res: any) => {
          this.data = res.data
          this.isLoading = false
        })
        .catch(() => {
          this.isLoading = false
        })
      this.api.getContractCount(this.query).then(res => {
        this.query.total = res.data
      })
    }
    this.forceRerender()
    this.$forceUpdate

    // this.handleExpandChange(data:{})
  }
  init(): void {
    this.isLoading = true
    this.checkTypeContract(this.$route?.name as string)
    const temp = {
      ...this.query,
      total: null,
      orderByClause: this.query.orderByClause ? this.query.orderByClause : null
      // contractStatus: this.$route.name === 'contract' ? '' : this.$route.name?.toUpperCase()
    }
    this.api
      .getData(temp)
      .then((res: any) => {
        this.data = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.api.getContractCount(temp).then(res => {
      this.query.total = res.data
    })
  }
  async handleExpandChange(data: { row: any; expandedRows: any }): Promise<void> {
    const temp = {
      bankContractNo: data.row.bankContractNo,
      searchType: 'CONTRACT'
    }
    const result: any = await this.api
      .getData(temp)
      .then((res: any) => {
        data.row.childrens = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.forceRerender()
    this.$forceUpdate
  }
