  action(): void {
    this.isConflickClick = true
  }
  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })

    this.query.orderByClause = command.id
    this.init()
  }
  getRelationShip(code: number): string {
    return RELATIONSHIP_VALUE_TO_TYPE[code]
  }
  indexMethod(index: number): number {
    return (this.query.page - 1) * this.query.limit + index + 1
  }
  setParamsForUser(): void {
    if (this.getDisableArea) {
      this.query.areaIdArray = `${this.user?.areaId}`
      this.query.branchIdArray = `${this.user?.branchId}`
    }
    if (this.getDisableTransaction) {
      this.query.transOfficeIdArray = `${this.user?.transactionOfficeId}`
    }
    if (this.getDisableSaleMember) {
      this.query.salesMemberIdArray = `${this.user?.id}`
    }
  }

  created(): void {
    this.api = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.query = assign(this.query, filters.filters)
    }
    // this.setParamsForUser()
    this.init()
  }

  checkStatus(item: Record<string, any>): string {
    let statusText = ''
    let classStatus = ''
    if (item.contractStatus === '1') {
      switch (item.expireStatus) {
        case '1':
          statusText = 'Mới'
          classStatus = 'effective'
          break
        case '2':
          statusText = 'Hủy'
          classStatus = 'cancel'
          break
        default:
          statusText = 'Mới'
          classStatus = 'effective'
          break
      }
    } else if (item.contractStatus === '2') {
      statusText = 'Hủy'
      classStatus = 'cancel'
    } else if (item.contractStatus === '3') {
      statusText = 'Tái tục'
      classStatus = 'effective'
    } else if (item.contractStatus === '4') {
      statusText = 'Mất hiệu lực'
      classStatus = 'lapse'
    }

    return '<span class="' + classStatus + ' statuss">' + statusText + '</span>'
  }

  handleExport(): void {
    let typeContract = ''
    let paymentName = 'contractAll'
    switch (this.$route.name) {
      case 'new':
        typeContract = 'hop_dong_moi'
        this.query.searchType = 'CONTRACT'
        this.query.contractStatus = '1'
        break
      case 'cancel':
        typeContract = 'hop_dong_huy'
        this.query.searchType = 'CONTRACT'
        this.query.contractStatus = '2'
        break
      case 'renew':
        typeContract = 'hop_dong_tai_tuc'
        this.query.searchType = 'CONTRACT'
        this.query.contractStatus = '3'
        break
      default:
        typeContract = 'hop_dong'
        this.query.searchType = 'CONTRACT'
        this.query.contractStatus = null
        break
    }
    this.handleExportExcel(paymentName, this.query, typeContract)
  }

  handleSelection(value: Array<Record<string, any>>, row: Record<string, any>): void {
    if (row) {
      const checked = value.find(item => item.id == row.id)
      if (checked && checked.id > 0) {
        let selection = this.data.filter((item: any) => item.bankContractNo === row?.bankContractNo && item.id !== row?.id)
        //@ts-ignore
        this.$refs.contractTable.updateSelection(selection)
        //@ts-ignore
        this.dataSelect = this.$refs.contractTable.getSelection()
      } else {
        this.dataSelect = value.filter((item: any) => item.bankContractNo !== row?.bankContractNo)
        //@ts-ignore
        this.$refs.contractTable.clearSelection()
        //@ts-ignore
        this.$refs.contractTable.updateSelection(this.dataSelect)
      }
      // if (row.contractStatus === '2') {
      //   this.isShowButton = true
      //   this.showButton = false
      // }
      // if (row.contractStatus !== '2') {
      //   this.isShowButton = false
      //   this.showButton = true
      // }
    } else {
      this.dataSelect = value
    }
  }

