  uploadFileCost(file: Record<string, any>): void {
    this.uploadFile(file)
  }
  uploadFile(file: Record<string, any>): void {
    const formData = new FormData()
    if (file.status === 'ready') {
      formData.append('file', file.raw)
      formData.append('name', file.name)
      formData.append('type', 'PAYMENT_FEE')
      const message = this.$message({
        message: 'Đang tải file lên',
        type: 'success',
        duration: 0
      })
      importReq
        .uploadFile(this.districtId, formData)
        .then(res => {
          let urlString = res.data.data[0].urlString
          this.api
            .uploadFileCost(urlString)
            .then(res => {
              if (res.data.length) {
                message.close()
                this.dataError = res.data
                this.fileName = file.name
                this.setOpenPopup({
                  popupName: 'popup-show-error',
                  isOpen: true
                })
              } else {
                message.close()
                this.$message.success('Tải lên thành công')
                this.init()
              }
            })
            .catch(error => {
              message.close()
              if (error.data && error.data.length) {
                this.dataError = error.data
                this.fileName = file.name
                this.setOpenPopup({
                  popupName: 'popup-show-error',
                  isOpen: true
                })
              } else {
                this.$message.error('Tải lên thất bại')
              }
            })
        })
        .catch(error => {
          message.close()
          if (error.errorCode === 429) {
            this.$message.error('Không upload được file khi hệ thống đang cập nhật dữ liệu. Vui lòng thử lại sau ít phút')
          } else {
            this.$message.error('Tải lên thất bại')
          }
        })
    }
  }
}
